const Plugin = require('./Plugin')
const html = require('yo-yo')

/**
 * Informer
 * Shows rad message bubbles
 * used like this: `bus.emit('informer', 'hello world', 'info', 5000)`
 * or for errors: `bus.emit('informer', 'Error uploading img.jpg', 'error', 5000)`
 *
 */
module.exports = class Informer extends Plugin {
  constructor (core, opts) {
    super(core, opts)
    this.type = 'progressindicator'
    this.id = 'Informer'
    this.title = 'Informer'
    this.timeoutID = undefined

    // set default options
    const defaultOptions = {}

    // merge default options with the ones set by user
    this.opts = Object.assign({}, defaultOptions, opts)
  }

  showInformer (msg, type, duration) {
    this.core.setState({
      informer: {
        isHidden: false,
        msg: msg
      }
    })

    window.clearTimeout(this.timeoutID)
    if (duration === 0) {
      this.timeoutID = undefined
      return
    }

    // hide the informer after `duration` milliseconds
    this.timeoutID = setTimeout(() => {
      const newInformer = Object.assign({}, this.core.getState().informer, {
        isHidden: true
      })
      this.core.setState({
        informer: newInformer
      })
    }, duration)
  }

  hideInformer () {
    const newInformer = Object.assign({}, this.core.getState().informer, {
      isHidden: true
    })
    this.core.setState({
      informer: newInformer
    })
  }

  render (state) {
    const msg = state.informer.msg
    const isHidden = state.informer.isHidden

    // @TODO add aria-live for screen-readers
    return (function () {
      
      var ac = require('/home/travis/build/transloadit/uppy/node_modules/yo-yoify/lib/appendChild.js')
      var bel1 = document.createElement("div")
bel1.setAttribute("aria-hidden", arguments[1])
bel1.setAttribute("class", "UppyInformer")
var bel0 = document.createElement("p")
ac(bel0, [arguments[0]])
ac(bel1, ["\n      ",bel0,"\n    "])
      return bel1
    }(msg,isHidden))
  }

  install () {
    // Set default state for Google Drive
    this.core.setState({
      informer: {
        isHidden: true,
        msg: ''
      }
    })

    const bus = this.core.bus

    bus.on('informer', (msg, type, duration) => {
      this.showInformer(msg, type, duration)
    })

    bus.on('informer:hide', () => {
      this.hideInformer()
    })

    const target = this.opts.target
    const plugin = this
    this.target = this.mount(target, plugin)
  }
}
